import { RendererCanvas } from '../../workshop-vector/base/RendererCanvas';

export class Solution extends RendererCanvas {
    t: number = 0.0;
    pixelList: Pixel[] = []
    constructor(div: HTMLDivElement) {
        super(div);
        this.ctx.translate(this.ctx.canvas.width * 0.5, this.ctx.canvas.height * 0.5);
        this.init();
    }
    public init() {
        this.pixelList = []
        const x = 10
        const y = 10
        const space = 50
        const x0 = x * 0.5 * -space
        const y0 = y * 0.5 * -space
        
        for(let j = 0 ; j <= y; j++) {
            for(let i = 0 ; i <= x; i++) {
                const x = i * space
                const y = j * space

                let pix = new Pixel(x0 + x, y0 + y, 0, space)
                this.pixelList.push(pix)
            }
        }

        this.start();
    }
    public render(ctx: CanvasRenderingContext2D): void {

        this.pixelList.forEach((p: Pixel) => {
            p.render(ctx);
        })
    }
    public mouseMove(x: number, y: number): void {
        //
    }

    public mouseClick(x: number, y: number): void {
        //
    }
}

class Pixel {
    vec: Vector3
    space: number = 0
    constructor(x: number, y: number, z: number, space) {
        this.vec = new Vector3(x,y,z)
        this.space = space;
    }
    public render(ctx: CanvasRenderingContext2D) {

        ctx.beginPath()
        ctx.rect(this.vec.x - this.space * 0.5, this.vec.y - this.space * 0.5, this.space, this.space)
        ctx.closePath()
        ctx.stroke()

        ctx.fillStyle = '#333';
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.vec.r, 0, Math.PI * 2.0);
        ctx.closePath();
        ctx.fill();
    }
}

class Vector3 {
    public x: number = 0.0;
    public y: number = 0.0;
    public z: number = 0.0;
    public r: number = 3
    constructor(x,y,z) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
}