export class RendererCanvas {
    public divHost: HTMLDivElement;
    public canvas: HTMLCanvasElement;
    public ctx: CanvasRenderingContext2D;
    public isDown: boolean = false;
    public debugMode: boolean = false;
    public isStatic: boolean = false;
    public m: number[] = [0,0];

    private prevent: boolean = false;
    private delay: number = 250;
    private timer: NodeJS.Timer | number;
    private isDrag: boolean = false;

    constructor(div: HTMLDivElement) {
        this.divHost = div;
        const bounding = this.divHost.getBoundingClientRect();
        this.canvas = document.createElement('canvas') as HTMLCanvasElement;
        // this.canvas.style.position = 'absolute';
        this.canvas.width = bounding.width;
        this.canvas.height = bounding.height;
        this.divHost.appendChild(this.canvas);
        this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;
        this.canvas.onclick = (e: MouseEvent) => {
            this.timer = setTimeout(() => {
                if (!this.prevent) {
                    this.mouseClick(e.offsetX, e.offsetY);
                }
                this.isDrag = false;
                this.prevent = false;
            },
                this.delay);
        };
        this.canvas.onwheel = (e: WheelEvent) => {
            this.mouseWheel(e);
        };
        this.canvas.ondblclick = (e: MouseEvent) => {
            this.mouseUpdate(e.offsetX, e.offsetY);
            clearTimeout(this.timer as number);
            this.prevent = true;
            this.mouseDoubleClick(e.offsetX, e.offsetY);
        };
        this.canvas.onmousedown = (e: MouseEvent) => {
            this.mouseUpdate(e.offsetX, e.offsetY);
            this.isDown = true;
            this.mouseDown(e.offsetX, e.offsetY);
        };
        this.canvas.onmouseup = (e: MouseEvent) => {
            this.mouseUpdate(e.offsetX, e.offsetY);
            this.isDown = false;
            this.isDrag = false;
            this.mouseUp(e.offsetX, e.offsetY);
        };
        this.canvas.onmousemove = (e: MouseEvent) => {
            this.mouseUpdate(e.offsetX, e.offsetY);
            if (this.isDown) {
                this.isDrag = true;
                this.prevent = true;
                this.mouseDrag(e.offsetX, e.offsetY);
            } else {
                this.mouseMove(e.offsetX, e.offsetY);
            }
        };
        window.addEventListener('resize', this.resize);
        // this.loop(this.ctx);
    }
    public start() {
        this.loop(this.ctx);
    }
    private resize = () => {
        const bounding = this.divHost.getBoundingClientRect();
        this.canvas.width = bounding.width;
        this.canvas.height = bounding.height;
    }
    public destroy() {
        window.removeEventListener('resize', this.resize);
        this.isStatic = true;
        try {
            while (this.divHost !== undefined && this.divHost.lastElementChild !== undefined) {
                this.divHost.removeChild(this.divHost.lastElementChild);
            }
        } catch (error) { }
        this.destroyPost();
    }
    public destroyPost() {

    }
    public loop(ctx: CanvasRenderingContext2D) {
        if (this.isStatic) { return; }
        requestAnimationFrame(() => { this.loop(ctx); });
        ctx.clearRect(ctx.canvas.width * -0.5, ctx.canvas.height * -0.5, ctx.canvas.width, ctx.canvas.height);
        this.render(ctx);
        this.renderPost();
    }
    public renderPost() {
        this.renderMouse(this.ctx, this.m[0], this.m[1]);
    }
    public render(ctx: CanvasRenderingContext2D) {
        // Binding needed
    }
    public mouseClick(x: number, y: number) {
        // Binding needed
        if (this.debugMode) { console.log('click'); }
    }
    public mouseDoubleClick(x: number, y: number) {
        // Binding needed
        if (this.debugMode) { console.log('d-click'); }
    }
    public mouseDown(x: number, y: number) {
        // Binding needed
        if (this.debugMode) { console.log('down'); }
    }
    public mouseUp(x: number, y: number) {
        // Binding needed
        if (this.debugMode) { console.log('up'); }
    }
    public mouseMove(x: number, y: number) {

        // Binding needed
        if (this.debugMode) { console.log('move'); }
    }
    public mouseDrag(x: number, y: number) {
        // Binding needed
        if (this.debugMode) { console.log('drag'); }
    }
    public mouseWheel(v: WheelEvent) {
        // Binding needed
        if (this.debugMode) { console.log('wheel', v); }
    }
    public renderMouse(ctx: CanvasRenderingContext2D, x: number, y: number) {
        ctx.fillStyle = '#f00';
        ctx.beginPath();
        ctx.arc(this.m[0], this.m[1], 3, 0, Math.PI * 2.0)
        ctx.fillText(`x: ${this.m[0]}, y:${this.m[1]}`,this.m[0] - 15, this.m[1] - 8)
        ctx.closePath();
        ctx.fill();
    }
    public mouseUpdate(x: number, y: number): void {
        this.m[0] = Math.floor(x - this.canvas.width * 0.5);
        this.m[1] = Math.floor(y - this.canvas.height * 0.5);
    }
}